@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

div#root {
  width: 100%;
  margin-bottom: 0;
}

h1,
p,
ul {
  margin: 0;
}

.txtEng {
  font-style: italic;
}

.link {
  cursor: pointer;
}
#headerLogo {
    width: 200px;
}

div.nav-bar-right ul li {
    list-style: none;
    text-decoration: none;
}

div.nav-bar-right ul li a {
    text-decoration: none;
}

.nav-bar div.alco {
    display: none;
}

nav.nav-bar {
    width: 100%;
    height: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
    background-color: #ffffff;
}

nav.nav-bar .nav-bar-right ul {
    padding: 0;
}

nav.nav-bar .nav-bar-right ul li {
    display: inline-block;
    width: 90px;
    text-align: center;
}

nav.nav-bar .nav-bar-right ul li a {
    color: #000000;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: bold;
    transition: all 0.5s ease;
}

nav.nav-bar .nav-bar-right ul li a:hover {
    color: #182c8c;
}

.nav-bar div.nav-bar-right.active {
    display: block;
}

/* Responsive section */
@media screen and (max-width: 768px) {
    #headerLogo {
        width: 150px;
    }

    nav.nav-bar {
        flex-direction: column;
        height: auto;
        align-items: center;
    }

    .nav-bar div.alco {
        display: block;
        position: absolute;
        top: 20px;
        right: 20px;
    }

    .nav-bar div.nav-bar-right ul li {
        display: block;
        padding: 10px;
    }

    .nav-bar div.nav-bar-right {
        display: none;
    }
}
footer {
    padding: 10px;
    color: white;
    background-color: #182c8c;
    text-align: center;
    font-weight: bold;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

